import Swipeable from "../../../components/Swipeable";
import SingleTeam from "../SingleTeam";

export default function TeamsList({ teams }) {
  return (
    <div className="teams-list">
      <Swipeable className="swipeable">
        {teams.map(({ slug, ...team }) => (
          <SingleTeam key={slug} {...team} />
        ))}
      </Swipeable>
    </div>
  );
}
